@import "ScssHelpers/helpers-all.scss";

.news_list_item {

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 4.7rem;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
		}
	}

	&:hover {
		.news_list_item__overlay {
			opacity: 0.77;
		}

		.news_list_item__read_more {
			opacity: 1;
			pointer-events: visible;
		}
	}
}

.news_list_item__link {

	&:hover,
	&:focus {
		text-decoration: none;
	}

	@include bp($point_4, min) {
		@include flex_block(row, nowrap, flex-start);
	}
}

.news_list_item__img_wrap {
	position: relative;

	width: 100%;
	height: 100%;


	@include bp($point_2, min) {
		flex-shrink: 0;
		padding-bottom: 19%;
	}

	@include bp($point_3, $point_4) {
		padding-bottom: 23%;
	}

	@include bp($point_4, min) {
		max-width: 36rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 50%;
		margin-bottom: .8rem;
	}
}

.news_list_item__overlay {
	@include stretch;
	@include transition_opacity;

	background-color: $gold_2;
	opacity: 0;
}

.news_list_item__img_w {
	display: block;
	width: 100%;
	height: 100%;
}

.news_list_item__img {
	@include stretch;
	@include cover_img;
}

.news_list_item__content {

	@include bp($point_2, min) {
		padding: .5rem 0 0 5.7rem;
	}

	@include bp($point_3, $point_4) {
		padding: 0 0 0 2rem;
	}
}

.news_list_item__subtitle {
	font-size: 1.8rem;
	font-weight: 500;
	letter-spacing: .105em;
	text-transform: uppercase;

	color: $gray_2;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1rem;
		}
		
		@include bp($point_3) {
			margin-bottom: .5rem;
		}
	}
}

.news_list_item__title {
	@include h3;

	line-height: 1.05;
	
	color: $black;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.4rem;
		}
		
		@include bp($point_3) {
			margin-bottom: 1rem;
		}
	}
}

.news_list_item__text {
	@include text;

	line-height: 1.5;

	color: $black;

	&:not(:last-child) {
		margin-bottom: 1.1rem;
	}
}

.news_list_item__read_more {
	@include flex_block(row, nowrap, flex-start, center);
	@include transition_opacity;

	color: $gold_2;
	font-size: 1.6rem;
	font-weight: 500;
	letter-spacing: -.02em;
	
	@include bp($point_4, min) {
		opacity: 0;
		pointer-events: none;
	}

	span {
		margin-right: .8rem;
		border-bottom: 1px solid currentColor;
	}

	&:hover {
		text-decoration: none;
		color: darken($gold_2, 10);
	}
}

.news_list_item__arrow_w {
	width: .6rem;
	height: 1.2rem;
}