@import "ScssHelpers/helpers-all.scss";

.nationwide {
	// padding-top: 5.4rem;
	// padding-bottom: 2rem;
}

.nationwide__title {
	@include h2;

	letter-spacing: -0.016em;
	line-height: 1;

	&:not(:last-child) {
		margin-bottom: 3.8rem;
	}
}

.nationwide__heading {
	@include h3;

	line-height: 1.2;
	letter-spacing: -0.016em;

	&:not(:last-child) {
		margin-bottom: 2.2rem;
	}
}

.nationwide__list {
	@include flex_block(row, wrap, flex-start, stretch);

	margin: 0 -2.6rem -2.6rem 0;
}

.nationwide__list_item {
	padding: 0 2.6rem 2.6rem 0;

	@include bp($point_2, min) {
		width: 33.333%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.nationwide__item {
	@include flex_block(column, nowrap, flex-end, flex-start);

	position: relative;

	width: 100%;
	height: 100%;

	min-height: 18.4rem;
	padding: 2.6rem 3.6rem;

	transition: opacity 0.3s;

	&:hover,
	&:focus {
		text-decoration: none;
		opacity: 0.8;
	}
}

.nationwide__item_img {
	@include stretch;
}

.nationwide__img {
	@include cover_img;
}

.nationwide__item_title {
	@include h3;

	position: relative;

	line-height: 1.2;
	letter-spacing: -0.016em;
	color: $white;
}
