@import "ScssHelpers/helpers-all.scss";

.account_content .woocommerce-orders-table {
	font-size: 1.6rem;

	@include bp($point_2, min) {
		margin-left: -3.6rem;
		width: calc(100% - 38rem);
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.account_content .woocommerce-orders-table__header {
	padding: 1.8rem 1.8rem;
	background-color: $beige_1;
	font-weight: 500;
	text-align: left;

	&:first-child {
		padding-left: 2rem;
	}

	&:last-child {
		padding-right: 0;
	}
}

.account_content .woocommerce-orders-table__cell {
	padding: 1rem 1.8rem;

	&:first-child {
		padding-left: 2rem;
	}

	&:last-child {
		padding-right: 0;
	}

	@include bp($point_2, min) {
		max-width: 24rem;
	}
	@include bp($point_3) {
	}
}

.account_content .woocommerce-orders-table__cell-order-number {
	font-weight: 500;
	text-decoration: underline;
}

.account_content .woocommerce-orders-table__row:first-child .woocommerce-orders-table__cell {
	padding-top: 1.3rem;
}

.account_content .woocommerce-orders-table__cell .woocommerce-button {
	position: relative;
	height: 4rem;
	padding: 0 1.5rem;
	font-size: 1.4rem;

	&:not(:last-child) {
		margin-right: 0.6rem;
	}
}

.account_content .woocommerce-orders-table__cell .woocommerce-button.view {
	padding-right: 2.5rem;
	padding-left: 1rem;

	&:after {
		content: "";
		position: absolute;
		right: 0.7rem;
		top: 50%;
		margin-top: -0.35rem;
		width: 1.2rem;
		height: 0.9rem;
		display: block;
		background-image: url("data:image/svg+xml;base64,");
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}
}
