@import "ScssHelpers/helpers-all.scss";

.cooking_tips_list {
	// padding-bottom: 7rem;
}

.cooking_tips_list__wrap {
}

.cooking_tips_list__items {
	@include flex_block(row, wrap, flex-start, flex-start);
	margin: 0 -2.4rem -2.4rem 0;
}

.cooking_tips_list__item {
	@include bp($point_2, min) {
		width: 25%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4, min) {
		padding: 0 5.8rem 2.4rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 2.4rem 2.4rem 0;
		width: 100%;
	}
}
