@import "ScssHelpers/helpers-all.scss";

.faq {
	// padding-top: 4.8rem;
	// padding-bottom: 0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			padding-bottom: 2.8rem;
		}
	}
}

.faq__body {
	@include bp($point_4, min) {
		max-width: 112rem;

		&:not(:last-child) {
			margin-bottom: 6.2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.faq__content {
}

.faq__title {
	@include h2;

	letter-spacing: -0.018em;
	border-bottom: 1px solid $beige_2;

	@include bp($point_4, min) {
		padding-bottom: 3.4rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 2.4rem;
	}
}

.faq__list {
	margin: 0;
	padding: 0;

	list-style: none;
}

.faq__item {
	border-bottom: 1px solid $beige_2;

	@include bp($point_4, min) {
		padding: 3rem 0 3.8rem;
	}

	@include bp($point_4 - 1) {
		padding: 2.4rem 0;
	}
}

.faq__item_head {
	@include flex_block(row, nowrap, flex-start, flex-start);

	cursor: pointer;
}

.faq__item_title {
	padding-right: 3rem;

	font-weight: 500;
	line-height: 1.3;

	@include bp($point_4, min) {
		font-size: 2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
	}
}

.faq__item_icon {
	margin: 8px 2px 0 auto;
	flex-shrink: 0;

	color: $gold_2;

	transition: color 0.3s ease;

	.faq__item--active_state & {
		transform: rotate(-180deg);
	}

	&:hover,
	&:focus {
		color: $brown_2;
	}

	@include transition_transform;

	@include bp($point_4, min) {
		width: 2.4rem;
		height: 1.2rem;
	}

	@include bp($point_4 - 1) {
		width: 2rem;
		height: 1rem;
	}
}

.faq__item_body {
	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.faq__item--active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.faq__item_wrap {
	padding-top: 1.4rem;
}

.faq__item_text {
	line-height: 1.5;
	color: $black;

	p:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include text;
}
