@import "ScssHelpers/helpers-all.scss";

.content_section {
}

.content_section__wrap {
	max-width: 112rem;
}

.content_section strong {
	display: block;

	font-weight: 400;

	@include bp($point_4, min) {
		font-size: 2.2rem;
		line-height: 1.4;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
		line-height: 1.5;
		letter-spacing: -0.016em;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.content_section p {
	@include text;

	line-height: 1.5;

	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	@include bp($point_4 - 1) {
		color: rgba(0, 0, 0, 0.8);
	}
}

.content_section h3 {
	font-size: 3.8rem;
	font-weight: 500;

	letter-spacing: -0.016em;
	line-height: 1.2;

	&:not(:last-child) {
		margin-bottom: 1.1rem;
	}
}
.content_section h4 {
	font-size: 2.8rem;
	font-weight: 500;

	line-height: 1.2;
	letter-spacing: -0.014em;

	&:not(:last-child) {
		margin-bottom: 0.8rem;
	}

	&:not(:first-child) {
		margin-top: 3.1rem;
	}
}

.content_section p + h3,
.content_section p + h4 {
	@include bp($point_3) {
		margin-top: 2.4rem;
	}
}
