@import "ScssHelpers/helpers-all.scss";

.retail_product_list {
	&:not(:last-child) {
		margin-bottom: 6rem;
	}
}

.retail_product_list__items {
	@include flex_block(row, wrap, flex-start, stretch);

	@include bp($point_4, min) {
		margin: 0 -2.4rem -5.8rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1.6rem -3.4rem 0;
	}
}

.retail_product_list__item {
	min-height: 100%;

	@include bp($point_4, min) {
		width: 25%;
		padding: 0 2.4rem 5.8rem 0;
	}

	@include bp($point_4 - 1) {
		width: 50%;
		padding: 0 1.6rem 3.4em 0;
	}
}
