@import "ScssHelpers/helpers-all.scss";

.product_subscription {
	width: 100%;
}

.product_subscription__subscribe {
	margin-bottom: 2.4rem;
	letter-spacing: -0.7px;
}

.product_subscription__list {
}

.product_subscription__item {
	@include flex_block(row, nowrap, flex-start, center, center);

	&:not(:last-child) {
		padding-bottom: 0.8rem;
	}
}

.product_subscription__name {
	flex-grow: 1;
	margin-right: 1rem;

	font-weight: 500;
	letter-spacing: 2px;
	line-height: 150%;
	text-transform: uppercase;

	color: $gray_2;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.product_subscription__data {
	font-weight: 400;
	line-height: 150%;

	color: $black;

	@include bp($point_2, min) {
		font-size: 1.6rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}
